</template>
<!-- Handlebars 模板 -->
{{#if ok}}
  <h1>Yes22</h1>
{{/if}}
<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '欢迎来到德莱联盟！哈哈'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}


</style>



